@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap);
:root {
    --light-text: #111517;
    --light-text-info: rgb(100, 100, 100);
    --light-foreground: #fff;
    --light-background: #fff;
    --light-box-shadow: #ebe5e5;
    --light-primary: #022e4b;
    --dark-text: #fff;
    --dark-text-info: rgb(235, 235, 235);
    --dark-foreground: #2b3945;
    --dark-background: #202c37;
    --dark-box-shadow: #2c3235;
    --dark-primary: #2b3945;
}

/*
Light :
text-color: #111517; foreground: #fff; background: #fff; box-shadow: #ebe5e5;
Dark : text-color: #fff; foreground: #2b3945; background: #202c37; box-shadow:
#2c3235;
*/

html, body {
    height: 100%;
}

body, * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans JP', sans-serif;
}

#root{
    height: 100%;
}

a {
    text-decoration: none !important;
    color: inherit;
}

.heading {
    font-weight: bold;
    font-size: 22px;
}

.sub-heading {
    font-weight: bold;
    font-size: 16px;
}

.info-text {
    font-size: 12px;
}

@media screen and (max-width: 576px) {
    .heading {
        font-weight: bold;
        font-size: 16px;
    }
    .sub-heading {
        font-weight: bold;
        font-size: 12px;
    }
}
/* The switch - the box around the slider */

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 23px;
}

/* Hide default HTML checkbox */

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.sun-icon.light{
    color: var(--dark-foreground);
}
.sun-icon.dark{
    color: var(--light-foreground);
}
/* The slider */

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: .4s;
}

.slider.light {
    background-color: var(--dark-foreground);
}

.slider.dark {
    background-color: var(--light-foreground);
}

.slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
}
.slider.light:before {
    background-color: var(--light-foreground);
}

.slider.dark:before {
    background-color: var(--dark-foreground);
}

.slider.light input:checked+.slider {
    background-color: var(--dark-foreground);
}

.slider.light input:focus+.slider {
    box-shadow: 0 0 1px var(--dark-foreground);
}

.slider.dark input:checked+.slider {
    background-color: var(--light-foreground);
}

.slider.dark input:focus+.slider {
    box-shadow: 0 0 1px var(--light-foreground);
}

input:checked+.slider:before {
    transform: translateX(37px);
}
.topnav {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    align-items: center;
}

.topnav.light {
    background-color: var(--light-foreground);
    border-bottom: 1px solid var(--light-box-shadow);
    box-shadow: 0px 1px 10px var(--light-box-shadow);
}

.topnav.dark {
    background-color: var(--dark-foreground);
    border-bottom: 1px solid var(--dark-box-shadow);
    box-shadow: 0px 1px 10px var(--dark-box-shadow);
}

.topnav .logo {
    max-width: 100%;
    width: 200px;
    padding: 5px;
}

@media only screen and (max-width: 400px) {
