@media only screen and (max-width: 768px) {
    /*tablets*/
    .col-lg-6 {
        width: 100%;
    }
    .col-1, .col-2, .col-3, .col-4, .col-5, .col-6 {
        width: 50%;
    }
}

@media only screen and (max-width: 576px) {
    /*mobile screens*/
    .col-lg-4, .col-lg-6, .col-1, .col-2, .col-3, .col-4, .col-5, .col-6 {
        width: 100%;
    }
}
.filter-menu {
    padding-top: 20px;
}

/* Search Box styles */

.search-container {
    position: relative;
}

.search {
    overflow: auto;
}

.filter-menu.light .search {
    color: var(--light-text);
    background-color: var(--light-foreground);
    border: 2px solid var(--light-text);
}

.filter-menu.dark .search {
    color: var(--dark-text);
    background-color: var(--dark-foreground);
    border: 2px solid var(--dark-text);
}

.search input[type="text"] {
    border: 0px;
    width: 67%;
    margin: 2px 0;
    padding: 8px 10px;
}

.search input[type="text"]:focus {
    outline: 0;
}

.filter-menu.light .search input[type="text"]:focus {
    color: var(--light-text-info);
}

.filter-menu.dark .search input[type="text"]:focus {
    color: var(--dark-text-info);
}

.search input[type="submit"] {
    border: 0px;
    background: none;
    float: right;
    padding: 10px;
    cursor: pointer;
}

.filter-menu.light .search input {
    background-color: var(--light-foreground);
}

.filter-menu.light .search input[type="submit"] {
    background-color: var(--light-primary);
    color: var(--dark-text);
}

.filter-menu.dark .search input {
    background-color: var(--dark-foreground);
    color: var(--dark-text);
}

.search-suggestions {
    position: absolute;
    z-index: 1000;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    right: 0;
}

.filter-menu.light .search-suggestions {
    border: 1px solid var(--light-box-shadow);
    background-color: var(--light-foreground);
    border-bottom: none;
    border-top: none;
}

.filter-menu.dark .search-suggestions {
    border: 1px solid var(--dark-box-shadow);
    background-color: var(--dark-foreground);
    border-bottom: none;
    border-top: none;
}

.search-suggestions ul {
    list-style-type: none;
}

.search-suggestions ul li {
    border-bottom: 1px solid #d4d4d4;
    padding: 5px;
    font-size: 14px;
}

.search-suggestions ul li:hover {
    cursor: pointer;
}

.filter-menu.light .search-suggestions ul li:hover {
    background-color: var(--light-box-shadow);
}

.filter-menu.dark .search-suggestions ul li:hover {
    background-color: var(--dark-box-shadow);
}

/* Dropdown Filter styles */

.region-select {
    position: relative;
    display: block;
    width: 100%;
}

.region-select select {
    overflow: auto;
    outline: none;
    background: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
    display: block;
    width: 100%;
    cursor: pointer;
    padding: 10px;
}

.filter-menu.light .region-select select {
    background-color: var(--light-foreground);
    border: 2px solid var(--light-text);
    color: var(--light-text-info);
}

.filter-menu.dark .region-select select {
    background-color: var(--dark-foreground);
    border: 2px solid var(--dark-text);
    color: var(--dark-text-info);
}

.filter-menu .region-select:after {
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 100%;
    line-height: 50px;
    content: '\25BC';
    text-align: center;
    z-index: 0;
    font-size: 12px;
    box-sizing: border-box;
}

.filter-menu.light .region-select:after {
    background: var(--light-primary);
    color: var(--dark-text);
    border: 2px solid var(--light-text);
}

.filter-menu.dark .region-select:after {
    background: var(--dark-primary);
    color: var(--dark-text);
    border: 2px solid var(--dark-text);
}
.card {
    cursor: pointer;
}

.flag-img {
    height: 100px;
    width: auto;
    max-width: 100%;
}

.card.light {
    background-color: var(--light-foreground);
    color: var(--light-text);
    box-shadow: 0px 1px 10px var(--light-box-shadow);
}

.card.dark {
    background-color: var(--dark-foreground);
    color: var(--dark-text);
    box-shadow: 0px 1px 10px var(--dark-box-shadow);
}

.card {
    height: 12em;
    width: 100%;
    border-radius: 16px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: width 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
}

/* ^-- The margin bottom is necessary for the drop shadow otherwise it gets clipped in certain cases. */

/* Top Half of card, image. */

.card-image {
    height: 8em;
    width: 100%;
    padding: 1em 2em;
    position: Absolute;
    top: 0px;
    transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-filter: grayscale(40%);
            filter: grayscale(40%);
}

/* Bottom Card Section */

.card-description {
    height: 4em;
    width: 100%;
    position: absolute;
    bottom: 0em;
    transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
    padding: 0.5em 1em;
}
.card-description .capital,.population{
    display: inline-block;
    width: 50%;
}
.card-description .population{
    padding-left: 5px;
}
.card-description .no-text-overflow{
    white-space: nowrap;
    overflow: hidden;
     text-overflow: ellipsis
}
.card.light .card-description{
    background-color: var(--light-foreground);
}
.card.dark .card-description{
    background-color: var(--dark-foreground);
}

/* Property Cards Hover States */

.card:hover .card-description {
    height: 0em;
    padding: 0px 1em;
}

.card:hover .card-image {
    height: 12em;
    -webkit-filter: none;
            filter: none;
}
.filtered-countries{
    padding-bottom: 20px;
}
.globe{
    margin-top: 50px;
    opacity: 80%;
    width: 150px;
}


.pagination-container {
    text-align: right;
    padding: 10px;
}

.pagination {
    display: inline-block;
}

.disabled {
    color: currentColor;
    cursor: not-allowed;
    opacity: 0.5;
    text-decoration: none;
}

.pagination a {
    float: left;
    padding: 2px 8px;
    font-size: 12px;
    text-decoration: none;
    transition: background-color 1s;
}

.pagination-container.light .pagination a {
    color: var(--light-text);
    border: 1px solid var(--light-text-info);
}

.pagination-container.dark .pagination a {
    color: var(--dark-text);
    border: 1px solid var(--dark-text-info);
}

.pagination-container.light .pagination a.active {
    background-color: var(--light-text);
    color: var(--dark-text);
}

.pagination-container.dark .pagination a.active {
    background-color: var(--dark-text);
    color: var(--light-text);
}

.pagination-container .pagination a:hover:not(.active) {
    background-color: var(--light-box-shadow);
    color: var(--light-text);
}
